<template>
  <div class="page-box index-box">
     <van-field v-model="num" placeholder="请输入数量" />
      <van-field
      style="margin-top: 20px"
    v-model="priviteStr"
    :placeholder="placeholder"
    :autosize="{ maxHeight: 500, minHeight: 200 }"
    type="textarea"
  >  </van-field>
  <van-button style="margin-top: 20px" type="primary" loading-text="交易中..." :loading="loading" @click="trade" size="large">交易</van-button>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import nftAbi from "@/utils/nftAbi";
import investAbi from "@/utils/investAbi";
import tokenAbi from "@/utils/abi";
import { Toast } from 'vant';
import BigNumber from 'bignumber.js';
import Common from '@ethereumjs/common'
import { Transaction } from '@ethereumjs/tx'
import { providers, Contract, Wallet }  from "ethers";
import { MaxUint256 } from "@ethersproject/constants";

export default {
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      num: '',
      privatekeyArr: [],
      timer: null,
      priviteStr: '',
      loading: false,
      placeholder: `請輸入私钥格式：
     348ce564d427a3311b6536bbcff9390d69395b06ed6c486954e971d960fe8709
     348ce564d427a3311b6536bbcff9390d69395b06ed6c486954e971d960fe8709
     348ce564d427a3311b6536bbcff9390d69395b06ed6c486954e971d960fe8709`,
      trade: async () =>{
         if(!data.num) {
          Toast('请输入交易数量')
          return
        }
        if(!data.priviteStr) {
          Toast('请输入私钥')
          return
        }
        data.loading = true
        let arr = data.priviteStr.split('\n')
        let priviteArrarr = arr.map(item => item.trim())
        data.priviteArrarr = priviteArrarr
        let num = Number(data.num)
        let amount = parseInt(num/priviteArrarr.length)
        priviteArrarr.forEach((item) => {
          data.userBetch(item, amount)
        })
        let provider = new providers.JsonRpcProvider('https://http-mainnet-node.huobichain.com/');
        let nftContract = new Contract(config.ElvesNftAddress, nftAbi, provider)
        let totalSuply = await nftContract.totalSupply()
        data.timer = setInterval(async () => {
            let totalSuply2 = await nftContract.totalSupply()
            if(Number(totalSuply2.toString()) - totalSuply.toString() >= num - 1) {
              clearInterval(data.timer)
               data.loading = false
            }
        }, 2000)
      },
      userBetch: async (privatekey, amount) => {
        
         let provider = new providers.JsonRpcProvider('https://http-mainnet-node.huobichain.com/');
         let wallet = new Wallet(privatekey, provider);
        let usdtContract = new Contract(config.USDT, tokenAbi, provider)
        let allowance = await usdtContract.allowance(wallet.address, config.ElvesKingAddress);
        if(Number(allowance) == 0) {
           let usdtContractWithSigner = usdtContract.connect(wallet);
           let usdttx = await usdtContractWithSigner.approve(config.ElvesKingAddress, MaxUint256);
           await usdttx.wait()
         }
        for(let i=0; i<amount; i++) {
          let balance = await usdtContract.balanceOf(wallet.address);
          if(Number(balance.toString()) < 269*(10**18)) {
            return
          }
           await data.openBlindBox(privatekey)
        }
      },
      openBlindBox: async (privatekey) => {
        let provider = new providers.JsonRpcProvider('https://http-mainnet-node.huobichain.com/');
        // let web3 = new Web3(new Web3.providers.HttpProvider('https://data-seed-prebsc-1-s1.binance.org:8545/'))
        // let contract = new web3.eth.Contract(investAbi, '0xaF7033ab28C04BC7c4056b3E36f7DfF2211e3A02')
        let contract = new Contract(config.ElvesKingAddress, investAbi, provider)
        // let nftContract = new web3.eth.Contract(nftAbi, '0xdA568338677cF17DfAd3897010cCEd1c93515AC0')
         let wallet = new Wallet(privatekey, provider);
         let contractWithSigner = contract.connect(wallet);
           let tx = await contractWithSigner.openBlindBox({gasLimit: 200000});
           tx.wait().then(() => {
             
           })
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

